import './App.scss';
import $ from 'jquery';
import Header from './components/Header';
import Footer from './components/Footer';
import React, { Suspense } from 'react';
import { useEffect } from 'react';
import { Route, Switch } from 'react-router-dom'
import { datosFirebaseUsuario } from './components/resources/Firebase';

// Importar vistas
const Links = React.lazy(() => import('./components/Links'))
const Login = React.lazy(() => import('./components/Login'))
const Register = React.lazy(() => import('./components/Register'))
const ResetPassword = React.lazy(() => import('./components/ResetPassword'))
const Admin = React.lazy(() => import('./components/Admin'))

function App() {
  useEffect(() => {
    datosFirebaseUsuario()
    .then((data) => {
        let background = data.background ? data.background : './img/fondo.jpg';
        $('body').css({
          background: 'linear-gradient(180deg, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, 0.2) 20%, rgba(0, 0, 0, 1) 100%), url('
          + background +')',
          'background-repeat': 'no-repeat',
          'background-size': 'cover',
          'background-color': '#000000' 
        });
    });    
  });

  return (
    <div className="container">
      <Header />
        <Suspense fallback={<div>s</div>}>
          <Switch>
            <Route exact path="/" render={() => <Links />} />
            <Route path="/login" render={() => <Login />} />
            <Route path="/register" render={() => <Register />} />
            <Route path="/admin" render={() => <Admin />} />
            <Route path="/resetpassword" render={() => <ResetPassword />} />
            <Route render={() => <Links />} />
          </Switch>
        </Suspense>
      <Footer />
    </div>
  );
}

export default App;
