export var auth = firebase.auth();
export var db = firebase.firestore();

export async function createUser() {
	await db.collection("users").doc(streamer)
	.set({
		registry: false,
		note: '',
		background: '',
		logo: ''
	});
	datosFirebaseUsuario()
}

export async function datosFirebaseUsuario() {
	var data = await db.collection("users").doc(streamer).get()
	if (data.exists){
		return data.data()
	} else {
		createUser()
	}
}

export async function datosFirebaseButtons() {
	var data = await db.collection("users").doc(streamer).collection("buttons").orderBy("timestamp").get()
	if (data.exists){
		return data
	} else {
		createUser()
	}
}