import { streamer } from './Config'

export async function Visitas(enlaceName) {
    await fetch(`https://api.countapi.xyz/info/${streamer}/${enlaceName}`)
	.then(response => response.json())
	.then(data => {
		if (data.namespace==null) {
			fetch(`https://api.countapi.xyz/create?enable_reset=1&namespace=${streamer}&key=${enlaceName}`)
			.then(
				fetch(`https://api.countapi.xyz/hit/${streamer}/${enlaceName}`)
			)
		} else {
			fetch(`https://api.countapi.xyz/hit/${streamer}/${enlaceName}`)
		}
	})
}

export function CheckImageURL(url) {
    return(url.match(/\.(jpeg|jpg|gif|png)$/) != null);
};

export function Capitalize(str) {
	let strFormat = str.charAt(0).toUpperCase() + str.substring(1);     
	return strFormat;
};

export function Zfill(nr, n, str){
    var ceros = Array(n-String(nr).length+1).join(str||'0')
    return `<span class="zfill">${ceros}</span>${nr}`
};