import './Header.scss'
import $ from 'jquery'
import { datosFirebaseUsuario, auth } from './resources/Firebase';
import { useHistory } from 'react-router-dom';
import { useEffect } from 'react';
import { streamer } from './resources/Config';

const Header = () => {
    let history = useHistory();

    useEffect(() => {
        datosFirebaseUsuario()
        .then((data) => {
            let logo = data.logo ? data.logo : './img/logo.png';
            $('#logo').attr({
                src: logo
            });
        });

        let contador = 0;
        console.log('( ͡° ͜ʖ ͡°) Toc toc, hay alguien?')
        obtainStatus()
        let minutos = 1
        let ms = minutos * 60 * 1000
        
        let rellamadas = setInterval(() => {
            console.log('( ͡° ͜ʖ ͡°) Sigues ahí...? ');
            obtainStatus()
        }, ms)

        $('.cargando').show()
        function obtainStatus() {
            contador++
            if (contador >= 5) {clearInterval(rellamadas)}
            $.get({
                url: `https://twitch-check.glitch.me/${streamer}`,
                cache: false,
                success : function(response){
                    if (response) {
                        $('.cargando').hide();
                        if (response.online) {
                            $('#texto-live').html('ONLINE: ' + response.stream_viewers + '<i class="fa fa-eye ml-1"></i>')
                            $('.logo-txt').addClass('logo-animation')
                            $('#texto-live').removeClass('texto-offline').addClass('texto-online')
                            $('#punto-live').removeClass('punto-offline').addClass('punto-online')
                            console.log('API: El usuario de twitch esta on fire')
                        } else {
                            $('#texto-live').text('OFFLINE')
                            $('.logo-txt').removeClass('logo-animation')
                            $('#texto-live').removeClass('texto-online').addClass('texto-offline')
                            $('#punto-live').removeClass('punto-online').addClass('punto-offline')
                            console.log('API: El usuario de twitch esta apagado o fuera de covertura')
                        }
                    } else {
                        console.log('( ͠° ͟ʖ ͡°) Volviendo a llamar a la puerta...')
                        setTimeout(() => {
                            obtainStatus()
                        },3000)
                    }
                }
            })
        }
    })

    function redirections() { 
        datosFirebaseUsuario()
        .then(data => {
            if (data.registry===true) {
                auth.onAuthStateChanged(user => {
                    if (!user) {
                        history.push('/login')
                    } else {
                        history.push('/admin')
                    }
                })
            } else {
                history.push('register')
            }
        })
    }

    return (
        <>
            <div className="row">
                <div className="banner">
                    <div className="col">
                        <button className="enlace" onClick={redirections}>
                            <h1 className="logo-txt">KEVINSILLO</h1>
                            <img className="logo-img" alt="Logo" id="logo" />
                        </button>
                        <div className="live d-flex">
                            <div id="punto-live"></div>
                            <div id="texto-live"></div>
                            <div className="cargando">Cargando status...</div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Header;