import './Footer.scss';
import { Visitas } from './resources/Helpers';

const Footer = () => {
    return (
        <footer className="footer text-light text-center text-lg-start fixed-bottom">
            <div className="text-center p-3">
                <span className="text-muted">Powered by </span>
                <a
                    rel="noreferrer"
                    href="https://www.instagram.com/kevinsillotwitch"
                    className="buttonToLink text-muted"
                    onClick={() => Visitas('creador')}>
                        @Kevinsillo
                </a>
                <br />
                <a
                    rel="noreferrer"
                    href="mailto:kevin.illanas@outlook.com?Subject=Petición%20de%20web"
                    className="buttonToLink text-muted"
                    onClick={() => Visitas('email')}>
                        Quiero una web como esta ಠ_ಠ
                </a>
            </div>
        </footer>
    )
}

export default Footer;